
@import "css/bootstrap.min.css";
@import "css/bootstrap-rtl.min.css";
@import "css/font-face.scss";
html {
    font-size: 16px;
}
body {
    direction: rtl;
    font-family: 'Iran Sans', sans-serif;
    font-size: 1rem;
    line-height: 1.8;
}
.numbers {
    text-align: center;
    direction: ltr;
}
[v-cloak] {
    display: none;
}
#app {
    padding-top: 3em;
}
.well p {
    margin: 0;
}
.panel {
    margin-bottom: 2em;
}
.panel-title {
    white-space: nowrap;
}
.panel-title a {
    color: #337ab7;
}
.panel-title a:hover {
    color: #23527c;
}
.ultra-light {
    font-weight: 200;
}
.light {
    font-weight: 300;
}
.medium {
    font-weight: 500;
}
.bold {
    font-weight: 700;
}
.black {
    font-weight: 900;
}



